@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin translate($x, $y) {  
    @include transform(translate($x, $y));
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
} 
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}

#cartSidebar {
        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
    }
    .cart-empty-inner {
        @include ab(50%,50%,n,n);
        margin-top: -108px;
        @include margin-start(-146px);
        svg {
            margin-bottom: 24px;
        }
        .h3 {
            font-size: 36px;
            font-weight: 600;
        }
    }
    input[type=number] {
          -moz-appearance: textfield;
    }
    .cart-item {
        align-items: center;
        padding: 20px 0;
    }
    .cart-table-body {
        .cart-item:not(:last-child) {
            border-bottom: 1px solid var(--miscellaneous-color);    
        }
    }
    .item-infor {
        align-items: center;
        @include padding-end(15px);
    }
    cart-remove-button {
        flex: 0 0 32px;
        max-width: 32px;
        a {
            width: 100%;
            height: 32px;
            display: flex;
            align-items: center;
            transition: 0.2s all ease-in-out;
            svg {
                width: 11px;
                height: 11px;
            }
            &:hover {
                opacity: 0.9;
            }
        }
        .loading {
            .icon-loading {
                display: block;
            }
            .icon-remove {
                display: none;
            }
        }
    }
    .card-group {
        .btn {
            .text {
                display: none;
            }
        }
    }
    .item-total {
        flex: 0 0 80px;
        @include margin-end(5px);
    }
    .item-media {
        width: 60px;
        @include margin-end(15px);
    }
    .item-detail {
        flex: 1;
    }
    .product-title a {
        font-size: 13px;
        line-height: 1.5em;
        font-weight: 400;
        margin-bottom: 5px;
        display: block;
    }
    .item-total-price {
        font-size: 14px;
    }
    .cart-item__quantity-wrapper {
        display: flex;
        align-items: center;
    }
    .quantity-select {
        display: flex;
        background: #f0f0f0;
        max-width: 100px;
        border-radius: var(--border-radius);
        position: relative;
        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            appearance: none;
            margin: 0;
        }
        input {
            height: 32px;
            flex: 1;
            padding: 5px;
            border: 0;
            text-align: center;
        }
        button {
            flex: 0 0 25px;
            max-width: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .icon-loading {
            display: none;
            width: 15px;
            height: 15px;
            @include ab(50%,50%,n,n);
            @include margin-start(-7.5px);
            margin-top: -7.5px;
        }
        &.loading {
            .icon-loading {
                display: block;
            }
            input {
                opacity: 0;
                visibility: hidden;
            }
        }
    }
    .cart-table {
        margin: 0 20px;
        overflow-y: auto;
    }
    .cart-table::-webkit-scrollbar-track {
        box-shadow: inset 0 0 4px var(--miscellaneous-color);
        -webkit-box-shadow: inset 0 0 var(--miscellaneous-color);
        background-color: var(--miscellaneous-color);
        border-radius: 5px;
    }
    .cart-table::-webkit-scrollbar {
        width: 4px;
        background-color: var(--miscellaneous-color);
        border-radius: 5px;
    }
    .cart-table::-webkit-scrollbar-thumb {
        background-color: var(--primary-color);
        border-radius: 5px;
    }
    .form-cart-section {
        display: flex;
        flex-direction: column;
        // height: calc(100vh - 90px);
        height: 100%;
        overflow: hidden;
        overflow-y: auto;
    }
    .cart-footer {
        margin-top: auto;
        .cart-totals {
            border-top: 1px solid var(--miscellaneous-color);
            align-items: center;
            padding: 20px 0 0;
            margin: 0 20px;
        }
        .totals {
            margin-bottom: 20px;
            align-items: center;
        }
        .totals__subtotal {
            font-size: 14px;
            font-weight: 700;
        }
        .totals__subtotal-value {
            margin: 0;
            font-size: 15px;
            font-weight: 500;
        }
        .cart-buttons {
            margin-left: 20px;
            margin-right: 20px;
            margin-bottom: 20px;
            .btn {
                display: flex;
                align-items: center;
                text-align: center;
                justify-content: center;
                width: 100%;
                margin-top: 12px;
            }
        }
    }
    cart-recommendation {
        display: block;
        position: relative;
        padding: 0 20px;
        &:before {
            content: "";
            @include ab(0,0,n,0);
            width: 100%;
            height: 1px;
            background: var(--miscellaneous-color);
        }
        .card {
            margin-bottom: 10px;
        }
    }
    .cart-recommend-title {
        font-size: 25px;
        font-weight: 500;
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .slider-controls {
        button {
            @include ab(40%,n,n,n);
            margin-top: -18px;
            width: 36px;
            height: 36px;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            background: var(--miscellaneous-color);
            svg {
                width: 14px;
                height: 14px;
                path {
                    fill: var(--primary-color);
                }
            }
        }
        .slider-prev-button {
            @include left(-15px);
        }
        .slider-next-button {
            @include right(-15px);
        }
    }
    @media (min-width: 750px) {
        .product-title a {
            font-size: 15px;
        }
        .item-total-price {
            font-size: 16px;
        }
        .card-group {
            .quantity-select button {
                flex: 0 0 24px;
                max-width: 24px;
            }
        }
        .cart-footer .cart-buttons {
            margin-bottom: 40px;
        }
        .cart-recommend-title {
            padding-top: 40px;
        }
    }
    @media (min-height: 700px) {
        .cart-footer {
            // @include ab(n,0,0,0);
